
@import 'https://fonts.googleapis.com/css?family=Roboto:300,400,500';
body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 1.5;
  padding: 4em 1em;
  color: #566b78;
}
h2 {
  margin-top: 1em;
  padding-top: 1em;
}
h3 {
  padding-top: 1em;
  padding-bottom: 0.5em;
}
h1,
h2,
strong {
  color: #333;
}
p {
  padding: 0.5em 0;
}
a {
  color: #e81c4f;
}
#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.passage-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1;
      flex: 1;
}
.verse-num {
  font-size: .875rem;
  color: #e81c4f;
}
.chapter-num {
  color: #e81c4f;
}
input[type="text"] {
  width: 100%;
  padding: 20px;
  text-align: center;
  border: 1px solid #566b78;
  border-radius: 5px;
  font-size: 1em;
  color: #333;
}
input[type="text"]:focus {
  outline: none;
}





